import { useEffect, useState } from "react";
import "./styles.css";

export default function App() {
  const [name, setName] = useState("Madhu");
  const [count, setCount] = useState(0);

  useEffect(() => {
    setName("Madhu");
  }, []);

  const add = () => {
    setCount(count + 1);
    console.log("clicked");
  };

  return (
    <div className="App">
      <>
        <header>header section of the page</header>

        <section>
          <h1>any contect by {name}</h1>
          <p>more content is smaller infront of size {count}</p>
          <button onClick={add}>add</button>
        </section>
      </>
    </div>
  );
}
